import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Layout, PageHeader, Button, message, Modal, Descriptions, } from 'antd';
import { FormOutlined, EditOutlined, DeleteOutlined } from '@ant-design/icons';
import Apis from '@/apis'
import { AdvanceTable, } from '@/components';
import EditForm from './edit'
const { Content } = Layout;
const confirm = Modal.confirm;
const valueTypeMap = {
  'string': '字符串',
  'number': '数值',
  'boolean': '布尔值',
}
const itemData = { title: '', name: '', options: [], valueType: '', type: '' }

@inject(({ appStore, authStore }) => ({
  setting: appStore.setting,
  user: authStore.user,
  permissions: authStore.permissions
}))
@observer
class ListView extends Component {
  editform = null
  state = {
    visible: false,
    params: {},
    editItem: {},
    update: 0
  }

  showModal = item => {
    this.setState({
      visible: true,
      editItem: { ...itemData, ...item }
    });
    if (this.editform) {
      this.editform.setFieldsValue({
        ...itemData, ...item,
      })
    }
  };

  hideModal = () => {
    this.setState({
      visible: false,
    });
  };

  okModal = () => {
    if (this.editform) {
      const { editItem } = this.state
      const { user } = this.props
      let newData = this.editform.getFieldsValue()
      if (newData.valueType && Array.isArray(newData.options)) {
        newData.options.forEach(item => {
          switch (newData.valueType) {
            case 'string':
              item.value = `${item.value}`
              break
            case 'number':
              item.value = Number(item.value)
              break
            case 'boolean':
              item.value = item.value === 'true'
              break
            default:
              break
          }
        })
      }
      if (!user.isSuper) newData.type = 2
      Apis[editItem._id ? 'dictionaryUpdate' : 'dictionaryCreate']({
        _id: editItem._id,
        ...newData
      }).then(res => {
        if (res) {
          message.success('操作成功!')
          this.setState({ update: +new Date() })
        } else {
          message.error('操作失败!')
        }
        this.setState({
          visible: false
        });
      })
    }
  };

  handleRemove = (_id, e) => {
    confirm({
      title: '你确定要删除所选字典吗?',
      okText: '确定',
      cancelText: '取消',
      onOk: () => {
        Apis.dictionaryRemove({ _id }).then(res => {
          if (res) {
            message.success('删除成功!')
            this.setState({ update: +new Date() })
          } else {
            message.error('删除失败!')
          }
        })
      },
    });
  }

  render() {
    const { editItem, visible, params, update } = this.state
    const { setting, user, } = this.props
    const columns = [
      {
        title: 'ID',
        dataIndex: '_id',
      },
      {
        title: '标题',
        dataIndex: 'title',
      },
      {
        title: '字典名',
        dataIndex: 'name',
      },
      {
        title: '值类型',
        dataIndex: 'valueType',
        render: val => valueTypeMap[val]
      },
      {
        title: '操作',
        show: true,
        render: row => (
          (user.isSuper || row.type !== 1) && <div>
            {<Button type="link" size="small" icon={<EditOutlined />} onClick={this.showModal.bind(this, row)}>编辑</Button>}
            {<Button type="link" size="small" icon={<DeleteOutlined />} onClick={this.handleRemove.bind(this, row._id)}>删除</Button>}
          </div>
        )
      },
    ];

    return (
      <Content>
        <PageHeader
          ghost={true}
          onBack={false}
          title={`基础数据管理`}
          className="page-title"
          extra={[
            <Button key="1" type="primary" icon={<FormOutlined />} onClick={this.showModal.bind(this, null)}>
              创建数据
            </Button>,
          ]}
        />
        {/* {permissions['manageDictionary/list'] ?  */}
        <Content>
          <div className="app-content">
            <AdvanceTable
              columns={columns}
              pageSize={setting.pageSize}
              size={'small'}
              params={{ ...params }}
              rowKey={record => record._id}
              showSetting={false}
              bordered={false}
              api={'dictionaryList'}
              update={update}
              expandable={{
                expandedRowRender: record => <Descriptions title={false} size={'small'} style={{ marginLeft: 50 }} column={1}>
                  {record.options.map(item => <Descriptions.Item label={`${item.key}`} key={item.key}>{item.value}</Descriptions.Item>)}
                </Descriptions>,
                rowExpandable: record => record.name !== 'Not Expandable',
              }}
            />
          </div>
        </Content>
        {/* : <Alert
          message="信息提示"
          description="没有权限查看内容, 请联系管理员."
          type="warning"
          showIcon
          style={{margin: '0 20px'}}
        />} */}
        <Modal
          title={`${editItem._id ? '编辑' : '创建'}字典`}
          visible={visible}
          onOk={this.okModal}
          onCancel={this.hideModal}
          okText="确认"
          cancelText="取消"
          width={700}
        >
          <EditForm data={{ ...editItem }} ref={el => this.editform = el} />
        </Modal>
      </Content>
    )
  }
};

export default ListView;
